@import "../abstracts/RFS";
// ===================================//

.buyer {
  &--image {
    overflow: hidden;
    position: relative;
    width: 20rem;
    height: 20rem;

    @include for-large-phone-only {
      width: 10rem;
      height: 10rem;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &--menu {
    margin: 3rem 0;
  }
  &--action {
    text-decoration: none;
  }

  &--content {
    align-self: center;
    display: grid;
  }
}

.image--overlay {
  position: absolute;
  bottom: 0;
  text-decoration: none;
  background-color: rgba(77, 77, 77, 0.562);
  width: 100%;
  height: 2rem;
  display: flex;
  align-items: center;
}
